body {
  letter-spacing: .04em;
  margin: 0;
  font-family: Poppins, sans-serif;
  font-style: normal;
  font-weight: 500;
  position: relative;
}

a {
  color: currentColor;
  text-decoration: none;
}

.visually-hidden {
  width: 1px;
  height: 1px;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  border: 0;
  margin: -1px;
  padding: 0;
  position: absolute;
  overflow: hidden;
}

button {
  cursor: pointer;
}

.light {
  color: #202025;
  fill: #202025;
  background-color: #fcfcfc;
}

.dark {
  color: #fcfcfc;
  fill: #fcfcfc;
  background-color: #202025;
}

body.disable-scroll {
  overflow: hidden;
}

::-webkit-scrollbar {
  width: 5px;
}

::-webkit-scrollbar-track {
  background: #bbb;
}

::-webkit-scrollbar-thumb {
  background: #fe7031;
}

p {
  margin: 0;
  padding: 0;
}

ul, ol, h1, h2, h3, h4, h5, h6 {
  margin: 0;
  padding: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

* {
  box-sizing: border-box;
}

.container {
  width: 100%;
  margin: 0 auto;
  padding-left: 20px;
  padding-right: 20px;
}

@media screen and (min-width: 480px) {
  .container {
    width: 480px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1280px) {
  .container {
    width: 1280px;
    padding-left: 16px;
    padding-right: 16px;
  }
}

.is-hidden {
  opacity: 0;
  pointer-events: none;
  visibility: hidden;
}

.section__header {
  padding-top: 20px;
  padding-bottom: 30px;
}

.header__container {
  justify-content: space-between;
  align-items: flex-end;
  display: flex;
  position: relative;
}

.header__logo--mobil {
  display: none;
}

.signup__btn {
  width: 40px;
  height: 40px;
  background: none;
  border: 1px solid gray;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  display: flex;
}

.signup__btn[data-authorithation="enable"] {
  border: 1px solid #fd5103;
}

.signup__btn[data-authorithation="enable"] .signup__ico--in {
  opacity: 0;
}

.signup__btn[data-authorithation="disable"] .signup__ico--in {
  opacity: 1;
  fill: gray;
}

.signup__btn[data-authorithation="disable"] .signup__ico--out {
  opacity: 0;
}

.signup__ico--out {
  fill: #fd5103;
}

.signup__btn--desktop {
  display: none;
}

.header__burger {
  width: 47px;
  height: 39px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  justify-content: center;
  align-items: center;
  display: flex;
}

.header__cross {
  z-index: 9;
  width: 47px;
  height: 39px;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  display: block;
  position: absolute;
  top: 22px;
  right: 20px;
}

.burger__img {
  fill: inherit;
}

.header__mobil {
  z-index: 8;
  width: 100%;
  height: 100%;
  padding: 27px 20px;
  position: absolute;
  top: 0;
  left: 0;
}

.switch {
  width: 40px;
  height: 20px;
  display: inline-block;
  position: relative;
}

.header__switcher--desktop {
  display: none;
}

.header__switcher {
  align-items: center;
  gap: 10px;
  margin-bottom: 44px;
  display: flex;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  cursor: pointer;
  background-color: #ccc;
  transition: all .4s;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
}

.slider:before {
  content: "";
  height: 26px;
  width: 26px;
  background-color: #fff;
  transition: all .4s;
  position: absolute;
  bottom: 3px;
  left: 2px;
}

input:checked + .slider {
  background-color: #fd5103;
  border: 1px solid #fcfcfc;
  border-radius: 20px;
}

input:focus + .slider {
  box-shadow: 0 0 1px #4d4d4d;
}

input:checked + .slider:before {
  background-color: #fcfcfc;
  transform: translateX(20px);
}

.slider.round {
  width: 40px;
  height: 20px;
  background-color: #fff;
  border: 1px solid #fd5103;
  border-radius: 20px;
}

.slider.round:before {
  width: 12px;
  height: 12px;
  background-color: #fd5103;
  border-radius: 50%;
}

.header__form--tablet {
  width: 288px;
  display: none;
}

.header__form {
  width: 100%;
  margin-bottom: 40px;
  position: relative;
}

.form__input {
  width: 100%;
  max-width: 288px;
  height: 40px;
  color: inherit;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fd5103;
  border-radius: 4px;
  padding-left: 48px;
  transition-property: outline;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.form__input::-ms-input-placeholder {
  color: #5f6775;
  opacity: .4;
}

.form__input::placeholder {
  color: #5f6775;
  opacity: .4;
}

.form__input:focus {
  outline: 1px solid #fd5103;
}

.form__btn {
  height: 38px;
  width: 40px;
  fill: #fd5103;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  border-right: 1px solid #fd5103;
  border-radius: 4px 0 0 4px / 5px 0 0 4px;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  top: 1px;
  left: 1px;
}

.header__nav--desktop {
  display: none;
}

.header__nav {
  flex-direction: column;
  gap: 30px;
  padding-bottom: 15px;
  display: flex;
  position: relative;
}

.nav__link {
  letter-spacing: .02em;
  color: inherit;
  cursor: pointer;
  font-family: Poppins;
  font-size: 28px;
  font-style: normal;
  font-weight: 400;
  line-height: .86;
  transition-property: color;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.nav__link:hover, .nav__link:focus {
  color: #fd5103;
}

.header__nav--desktop {
  display: none;
}

.nav__links {
  z-index: 2;
  min-width: 213px;
  border: 1px solid #fd5103;
  border-radius: 4px;
  position: absolute;
  bottom: 0;
  transform: translateY(100%);
}

.nav__links--desktop {
  transform: translate(69%, 110%);
}

.nav__menu {
  display: flex;
}

.nav__item--top {
  letter-spacing: .02em;
  transition-property: background-color color;
  background-color: rgba(0, 0, 0, 0);
  padding: 12px;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.33;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.nav__item--top:hover, .nav__item--top:focus {
  color: #fff;
  background-color: #fd5103;
}

.nav__item--bottom {
  letter-spacing: .02em;
  transition-property: background-color color;
  background-color: rgba(0, 0, 0, 0);
  padding: 12px;
  font-size: 18px;
  font-weight: 400;
  line-height: 1.33;
  transition-duration: .25s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
}

.nav__item--bottom:hover, .nav__item--bottom:focus {
  color: #fff;
  background-color: #fd5103;
}

.header__light {
  color: #fd5103;
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.71;
}

.header__dark {
  font-family: Poppins;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.71;
}

@media screen and (max-width: 767.7px) {
  .container {
    max-width: 480px;
  }

  .header__container {
    align-items: center;
  }
}

@media screen and (min-width: 768px) {
  .header__mobil {
    flex-direction: column;
    align-items: center;
    display: flex;
  }

  .header__form--tablet {
    margin-left: 200px;
    display: block;
    position: relative;
  }

  .header__form {
    display: none;
  }

  .header__logo--mobil {
    display: block;
    position: absolute;
    left: 20px;
  }

  .form__input, .form__btn {
    background-color: rgba(0, 0, 0, 0);
  }

  .header__switcher {
