  margin-right: auto;
}

@media screen and (min-width: 768px) {
  .custom-select {
    margin-left: 0;
  }
}

details[open] {
  z-index: 1;
}

summary {
  width: 93px;
  height: 34px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0);
  border: 1px solid #fd5103;
  border-radius: 4px;
  justify-content: space-between;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: flex;
}

summary::-webkit-details-marker {
  display: none;
}

details[open] summary:before {
  content: "";
  width: 100vw;
  height: 100vh;
  background: none;
  display: block;
  position: fixed;
  top: 0;
  left: 0;
}

summary:after {
  content: "";
  width: 8px;
  height: 8px;
  border-bottom: 2px solid #fd5103;
  border-left: 2px solid #fd5103;
  border-bottom-left-radius: 2px;
  margin: 2px 8px 8px 6px;
  transition: transform .1s ease-in-out;
  display: block;
  transform: rotate(45deg)translate(50%);
}

summary:focus {
  outline: none;
}

details[open] summary:after {
  transform: rotate(-45deg)translate(0%);
}

.select-item {
  margin: 0;
  padding: 10px;
}

.select-item:hover {
  background-color: rgba(253, 82, 3, .184);
}

summary.radios {
  counter-reset: radios;
}

summary.radios:before {
  content: var(--selection);
}

input[type="radio"] {
  counter-increment: radios;
  -webkit-appearance: none;
  -ms-appearance: none;
  appearance: none;
  display: none;
}

input[type="radio"]:checked {
  --display: block;
  display: inline;
}

input[type="radio"]:after {
  content: attr(title);
  color: #fd5103;
  padding-left: 7px;
  font-size: 16px;
  display: block;
}

.select-list {
  counter-reset: labels;
  width: 93px;
  height: 140px;
  max-height: 200px;
  background: #fff;
  border: 1px solid #fd5103;
  border-radius: 4px;
  margin: 0;
  padding: 0;
  list-style: none;
  position: absolute;
  top: 100%;
  left: 50%;
  overflow-y: auto;
  transform: translate(-50%);
}

label {
  width: 100%;
  cursor: pointer;
  color: #5f6775;
  justify-content: space-between;
